* {
	box-sizing: border-box;
}
html,
body {
	background: #f8f8f8;
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding: 0;
	width: 100%;
}

h1,
h2,
h3,
h4 {
	font-family: Tahoma, Geneva, sans-serif;
	margin: 0;
	margin-bottom: 1rem;
}
h1 a,
h2 a,
h3 a,
h4 a {
	color: inherit;
}
h1 {
	font-size: 1.728rem;
}
h2 {
	font-size: 1.44rem;
}
h3 {
	font-size: 1.2rem;
}
h4 {
	font-size: 1rem;
}

a:not(:hover) {
	text-decoration: none;
}
::selection {
	background: #f93;
	color: #fff;
}
::-moz-selection {
	background: #f93;
	color: #fff;
}
/*colors*/
header,
footer {
	background: #222;
	color: #fff;
}
main {
	color: #222;
}
/*links*/
a {
	color: #09c;
}
a:hover {
	color: #f90;
	cursor: pointer;
	text-decoration: underline;
}
